import React from 'react'
import styled from 'styled-components'

const Nav: React.FC = () => {
    return (
        <StyledNav>
            <StyledLink
                target="_blank"
                href="https://etherscan.io/address/0x658e5173124e4fd48f2266535e6e894a248471cc"
            >
                ForestGuard Contract
      </StyledLink>
            <StyledLink
                target="_blank"
                href="https://app.uniswap.org/#/add/0xf7Eb799E402F3cAC0e042928853d516775E66F8e/ETH"
            >
                Uniswap FOREST-ETH
      </StyledLink>
            <StyledLink target="_blank" href="https://etherscan.io/token/0xf7Eb799E402F3cAC0e042928853d516775E66F8e">
                FOREST Token
      </StyledLink>
            <StyledLink target="_blank" href="https://etherscan.io/address/0x912B2e194F3207B01c8d884Fc26Aa30937a41b17">
                ForestRouterV2 Contract
      </StyledLink>
            <StyledLink target="_blank" href="https://thegraph.com/explorer/subgraph/theomnipotentbeing/forestguardfinance">
                Graph Query
            </StyledLink>
            <StyledLink target="_blank" href="https://github.com/forestguard">
                Github
            </StyledLink>
        </StyledNav>
    )
}

const StyledNav = styled.nav`
  align-items: center;
  display: flex;
`

const StyledLink = styled.a`
  font-family: 'Trade Winds', sans-serif;
  color: ${(props) => props.theme.color.grey[400]};
  padding-left: ${(props) => props.theme.spacing[3]}px;
  padding-right: ${(props) => props.theme.spacing[3]}px;
  text-decoration: none;
  &:hover {
    color: ${(props) => props.theme.color.grey[500]};
  }
`

export default Nav
