import React from 'react'
import { NavLink } from 'react-router-dom'
import styled from 'styled-components'

const Nav: React.FC = () => {
    return (
        <StyledNav>
            <StyledLink exact activeClassName="active" to="/">
                Home
      </StyledLink>
            <StyledLinkurl target="_blank" href="https://app.simplenote.com/p/nQ9ggL">
                Buy/Sell_FOREST
      </StyledLinkurl>
            <StyledLink exact activeClassName="active" to="/farms">
                Dungeons
      </StyledLink>
            <StyledLinkurl target="_blank" href="https://forestguard.github.io/swap/">
                ForestSwap
      </StyledLinkurl>
            <StyledLinkurl target="_blank" href="http://forestguard.github.io/sight">
                ForestSightVision
      </StyledLinkurl>
            <StyledLinkurl target="_blank" href="https://t.me/forestguardfinance">
                Telegram
      </StyledLinkurl>
        </StyledNav>
    )
}

const StyledNav = styled.nav`
  align-items: center;
  display: flex;
`
const StyledLinkurl = styled.a`
  font-family: 'Trade Winds', sans-serif;
  color: ${(props) => props.theme.color.grey[400]};
  padding-left: ${(props) => props.theme.spacing[3]}px;
  padding-right: ${(props) => props.theme.spacing[3]}px;
  text-decoration: none;
  &:hover {
    color: ${(props) => props.theme.color.grey[500]};
  }
`
const StyledLink = styled(NavLink)`
  font-family: 'Trade Winds', sans-serif;
  color: ${(props) => props.theme.color.grey[400]};
  font-weight: 700;
  padding-left: ${(props) => props.theme.spacing[3]}px;
  padding-right: ${(props) => props.theme.spacing[3]}px;
  text-decoration: none;
  &:hover {
    color: ${(props) => props.theme.color.grey[500]};
  }
  &.active {
    color: ${(props) => props.theme.color.primary.main};
  }
  @media (max-width: 400px) {
    padding-left: ${(props) => props.theme.spacing[2]}px;
    padding-right: ${(props) => props.theme.spacing[2]}px;
  }
`

const StyledAbsoluteLink = styled.a`
  color: ${(props) => props.theme.color.grey[400]};
  font-weight: 700;
  padding-left: ${(props) => props.theme.spacing[3]}px;
  padding-right: ${(props) => props.theme.spacing[3]}px;
  text-decoration: none;
  &:hover {
    color: ${(props) => props.theme.color.grey[500]};
  }
  &.active {
    color: ${(props) => props.theme.color.primary.main};
  }
  @media (max-width: 400px) {
    padding-left: ${(props) => props.theme.spacing[2]}px;
    padding-right: ${(props) => props.theme.spacing[2]}px;
  }
`

export default Nav
